import { Box, Drawer, IconButton, List, ListItem, ListItemIcon, ListItemText, makeStyles } from '@material-ui/core'
import AddIcon from '@material-ui/icons/Add'
import RemoveIcon from '@material-ui/icons/Remove'
import SettingsIcon from '@material-ui/icons/Settings'
import React from 'react'
import Hammer from 'react-hammerjs'

import { AuthProps, withAuth } from '../components/Auth'
import { withLayout } from '../components/Layout'
import showImage from '../components/showImage'
import useObject from '../components/useObject'
// import { VideoSRC } from '../components/useVideo'
import Video from '../images/video.mp4'

interface Props extends AuthProps {}

const useStyles = makeStyles({
  drawer: {
    width: 280,
  },
  settingButton: {
    position: 'fixed',
    top: 0,
    right: 0,
    color: 'rgba(255, 255, 255, 0.1)',
  },
})

const Wallpaper = ({ password }: Props) => {
  const { drawer, settingButton } = useStyles()
  const { render, next, list } = showImage({
    password,
  })

  const [open, setOpen] = React.useState(false)
  const [config, setConfig] = useObject({
    interval: 0,
  })
  // const { video, ref } = useVideo()
  const videoRef = React.createRef<HTMLVideoElement>()

  const play = React.useCallback(() => {
    if (config.interval === 0) { return }
    if (!videoRef.current) { return }
    const v = videoRef.current
    if (v.paused) { v.play() }
  }, [config.interval, videoRef])

  React.useEffect(() => {
    if (config.interval === 0) { return }
    play()
    const t = setInterval(next, config.interval * 1000)
    return () => clearInterval(t)
  }, [config.interval, next, play])

  React.useEffect(() => {
    if (!videoRef.current) { return }
    videoRef.current.load()
  }, [videoRef])

  return (
    <Hammer
      direction="DIRECTION_ALL"
      onSwipe={(evt) => {
        // if (scale > 1) { return }
        if (evt.direction === 2 || evt.direction === 4) {
          // direction left or right
          next()
        }
        // if (evt.direction === 16) {
        //   // direction down
        //   setOptions({ ...options, openMenu: true })
        // }
      }}
    >
      <Box>
        <video
          ref={videoRef}
          playsInline
          // loop
          muted
          width={300}
          height={300}
          onEnded={play}
        >
          <source src={Video} type="video/mp4" />
        </video>
        {render}
        <IconButton
          color="secondary"
          className={settingButton}
          onClick={() => setOpen(true)}
        >
          <SettingsIcon />
        </IconButton>
        <Drawer
          open={open}
          onClose={() => setOpen(false)}
          anchor="right"
        >
          <Box className={drawer}>
            <List>
              <ListItem>
                <ListItemText
                  primary={list.length}
                  secondary="Total Image"
                />
              </ListItem>
              <ListItem>
                <ListItemIcon>
                  <IconButton
                    color="secondary"
                    onClick={() => setConfig({ interval: config.interval + 1 })}
                  >
                    <AddIcon />
                  </IconButton>
                </ListItemIcon>
                <ListItemText
                  primary={config.interval}
                  secondary="Automation Interval"
                />
                <ListItemIcon>
                  <IconButton
                    color="secondary"
                    onClick={() => {
                      if (config.interval === 0) { return }
                      setConfig({ interval: config.interval - 1 })
                    }}
                  >
                    <RemoveIcon />
                  </IconButton>
                </ListItemIcon>
              </ListItem>
            </List>
          </Box>
        </Drawer>
      </Box>
    </Hammer>
  )
}

export default withLayout(withAuth(Wallpaper, {
  required: true,
}), {
  title: 'Wallpaper',
  disableHeader: true,
  theme: 'dark',
})
