import { Box, makeStyles, useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'

import { AuthProps } from './Auth'
import useImages from './useImages'

interface Props extends AuthProps {
  hideBackground?: boolean
  hideForeground?: boolean
}

interface StyleProps {
  image: string
  background: string
  offset: number
}

const useStyles = makeStyles((theme) => ({
  container: {
    width: '100vw',
    height: '100vh',
  },
  hidden: {
    display: 'none',
  },
  image: ({ image }: StyleProps) => ({
    width: '100%',
    height: '100%',
    backgroundSize: 'contain',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    backgroundImage: image && `url(${image})`,
    transition: theme.transitions.create(['background'], {
      duration: theme.transitions.duration.complex,
      easing: theme.transitions.easing.easeInOut,
    }),
  }),
  background: ({ offset, background }: StyleProps) => ({
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    zIndex: -1,
    backgroundPosition: `${offset}px center`,
    backgroundImage: background && `url(${background})`,
    transition: theme.transitions.create(['background'], {
      duration: theme.transitions.duration.shortest,
      easing: theme.transitions.easing.easeInOut,
      delay: 250,
    }),
    '&::after': {
      content: '""',
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      backgroundColor: 'rgba(8, 8, 8, 0.92)',
    },
  }),
}))

const PRELOAD = 5

const Image = ({ password, hideBackground = false, hideForeground = false }: Props) => {
  const theme = useTheme()
  const isLargerThanMD = useMediaQuery(theme.breakpoints.up('md'))

  const [list, setList] = useImages(password, PRELOAD)
  const selectedImage = React.useMemo(() => list.find((_, i) => i === 0), [list])
  const backgroundImage = React.useMemo(() => list.find((_, i) => i === 1), [list])

  const next = React.useCallback(() => {
    setList((l) => l.slice(1))
  }, [setList])

  const offset = React.useMemo(() => {
    if (!backgroundImage) { return 0 }
    return backgroundImage.width * 0.2 * -1
  }, [backgroundImage])

  const { container, hidden, image, background } = useStyles({
    offset,
    image: selectedImage ? selectedImage.url : '',
    background: backgroundImage ? backgroundImage.url : '',
  })

  const render = (
    <Box className={container}>
      {list.slice(0, PRELOAD).map((img) => (
        <img key={img.id} className={hidden} src={img.url} />
      ))}
      {backgroundImage && isLargerThanMD && !hideBackground && (
        <Box className={background} />
      )}
      {selectedImage && !hideForeground && (
        <Box className={image} />
      )}
    </Box>
  )

  return { render, next, list }
}

export default Image
